import React, { useState, useEffect } from 'react';
import MainCard from '../../../ui-component/cards/MainCard';
import ConfirmDialog from '../../../ui-component/ConfirmDialog/ConfirmDialog';
import { useDispatch, useSelector } from 'react-redux';
import { getAllQA, shiftQAUp, shiftQADown, deleteQA } from '../../../store/actions/FAQActions';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    IconButton,
    Button,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment,
    Tooltip
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import { useParams } from 'react-router-dom';
import EditIcon from '@material-ui/icons/Edit';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import SubjectIcon from '@material-ui/icons/Subject';
import AddUpdateQA from './add-qa';
import useStyles from '../../ViewTableStyles';
import useUserRole from '../../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function AllQAs() {
    const classes = useStyles();
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });
    const dispatch = useDispatch();
    const { id } = useParams();

    const [value, setValue] = useState('');
    const [openAddUpdateQAs, setOpenAddUpdateQAs] = useState(false);

    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(10);

    const [confirmDialog, setConfirmDialog] = useState({
        isOpen: false,
        title: '',
        subTitle: ''
    });
    const [isQAUpdateData, setQAUpdateData] = useState(null);

    const { allQAs } = useSelector((state) => state?.faq);

    const newData = allQAs?.content;

    const handleDelete = async (mid) => {
        setConfirmDialog({
            ...confirmDialog,
            isOpen: false
        });
        dispatch(
            deleteQA(mid, () => {
                dispatch(getAllQA(id));
            })
        );
    };

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    const handleQA = (id) => {
        setOpenAddUpdateQAs(true);
        setQAUpdateData(id);
    };

    const handleShiftUp = (mid) => {
        dispatch(
            shiftQAUp(mid, () => {
                dispatch(getAllQA(id));
            })
        );
    };
    const handleShiftDown = (mid) => {
        dispatch(
            shiftQADown(mid, () => {
                dispatch(getAllQA(id));
            })
        );
    };
    useEffect(() => {
        dispatch(getAllQA(id, rowsPerPage, value ? value : '', page + 1));
    }, [rowsPerPage, value, page]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; All QAs </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div>
                            <OutlinedInput
                                className={classes.searchControl}
                                id="input-search-header"
                                value={value}
                                onChange={(e) => setValue(e.target.value)}
                                placeholder="Search on QUESTIONS"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </div>
                        <div>
                            <Button
                                variant="contained"
                                color="primary"
                                onClick={() => {
                                    setOpenAddUpdateQAs(true);
                                    setQAUpdateData(null);
                                }}
                                className={classes.btnAdd}
                            >
                                Add QAs
                            </Button>
                        </div>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Question </TableCell>
                                    <TableCell> Answer </TableCell>
                                    <TableCell align="center"> Shift </TableCell>
                                    <TableCell align="right"> Action </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {newData?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell> {row?.question} </TableCell>
                                            <TableCell> {row?.answer} </TableCell>
                                            <TableCell align="center" style={{ cursor: 'pointer' }}>
                                                <ArrowUpwardIcon onClick={() => handleShiftUp(row?.id)} />
                                                <ArrowDownwardIcon onClick={() => handleShiftDown(row?.id)} />
                                            </TableCell>
                                            <TableCell align="right">
                                                <Tooltip title="Update QA">
                                                    <IconButton onClick={() => handleQA(row)}>
                                                        <EditIcon color="primary" style={{ cursor: 'pointer' }} />
                                                    </IconButton>
                                                </Tooltip>

                                                <IconButton
                                                    onClick={() => {
                                                        setConfirmDialog({
                                                            isOpen: true,
                                                            title: 'Delete QA',
                                                            subTitle: 'Are you sure you want to delete this QA.',
                                                            onConfirm: () => {
                                                                handleDelete(row?.id);
                                                            }
                                                        });
                                                    }}
                                                >
                                                    <DeleteForeverIcon color="error" style={{ cursor: 'pointer' }} />
                                                </IconButton>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[10, 15, 25]}
                        component="div"
                        count={allQAs?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
                {openAddUpdateQAs && (
                    <AddUpdateQA
                        openAddUpdateQAs={openAddUpdateQAs}
                        setOpenAddUpdateQAs={setOpenAddUpdateQAs}
                        isEditData={isQAUpdateData}
                        QAId={id}
                    />
                )}
                {confirmDialog && <ConfirmDialog confirmDialog={confirmDialog} setConfirmDialog={setConfirmDialog} />}
            </MainCard>
        </>
    );
}
