import { http } from '../../../utils/http';
import { setMessage } from '../MessageActions';
import {
    GET_ALL_FAQ_START,
    GET_ALL_FAQ_SUCCESS,
    GET_ALL_FAQ_ERROR,
    GET_FAQ_START,
    GET_FAQ_SUCCESS,
    GET_FAQ_ERROR,
    GET_ALL_QA_START,
    GET_ALL_QA_SUCCESS,
    GET_ALL_QA_ERROR,
    GET_QA_START,
    GET_QA_SUCCESS,
    GET_QA_ERROR
} from '../../constants';

const getFAQStart = () => {
    return { type: GET_FAQ_START };
};
const getFAQSuccess = (payload) => {
    return { type: GET_FAQ_SUCCESS, payload };
};
const getFAQError = (error) => {
    return { type: GET_FAQ_ERROR, error };
};

const getAllFAQStart = () => {
    return { type: GET_ALL_FAQ_START };
};
const getAllFAQSuccess = (payload) => {
    return { type: GET_ALL_FAQ_SUCCESS, payload };
};
const getAllFAQError = (error) => {
    return { type: GET_ALL_FAQ_ERROR, error };
};

const getQAStart = () => {
    return { type: GET_QA_START };
};
const getQASuccess = (payload) => {
    return { type: GET_QA_SUCCESS, payload };
};
const getQAError = (error) => {
    return { type: GET_QA_ERROR, error };
};

const getAllQAStart = () => {
    return { type: GET_ALL_QA_START };
};
const getAllQASuccess = (payload) => {
    return { type: GET_ALL_QA_SUCCESS, payload };
};
const getAllQAError = (error) => {
    return { type: GET_ALL_QA_ERROR, error };
};

const addFAQ = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'POST', 'documents-service/faqs-service/faq-categories', payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const addModuleSubscription = (payload, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'POST', 'subscriptions-service/module-subscriptions', payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const addPlanSubscription = (payload, successCB) => async (dispatch) => {
    try {
        const res = await http(process.env.REACT_APP_ROOT_URL_SUBSCRIPTIONS, 'POST', 'subscriptions-service/plan-subscriptions', payload, true);
        dispatch(setMessage(res?.message));
        successCB && successCB(res);
        return res;
    } catch (error) {
        console.error(error);
        dispatch(setMessage(error?.data?.message));
        throw error;
    }
};


const addQA = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'POST', `documents-service/faqs-service/faqs/category/${id}`, payload, true)
            .then((res) => {
                dispatch(setMessage(res?.message ? res?.message : 'QA Added Successfully'));
                successCB && successCB(res);
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const updateFAQ = (payload, id, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'PUT', `documents-service/faqs-service/faq-categories/${id}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const updateQA = (payload, mid, successCB) => (dispatch) => {
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'PUT', `documents-service/faqs-service/faqs/${mid}`, payload, true)
            .then((res) => {
                if (res) {
                    dispatch(setMessage(res?.message));
                    successCB && successCB(res);
                }
            })
            .catch((err) => {
                console.log(err);
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log(error);
    }
};

const getFAQ = (id, successCB) => (dispatch) => {
    dispatch(getFAQStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'GET', `documents-service/faqs-service/faq-categories/${id}`, true)
            .then((res) => {
                const payload = {
                    getFAQData: res
                };
                dispatch(getFAQSuccess(payload));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(getFAQError(err));
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        dispatch(getFAQError('Something went wrong'));
    }
};

const getQA = (mid, successCB) => (dispatch) => {
    dispatch(getQAStart());
    try {
        http(process.env.REACT_APP_ROOT_URL_FAQ, 'GET', `documents-service/faqs-service/faqs/${mid}`, true)
            .then((res) => {
                const payload = {
                    getQAData: res
                };
                dispatch(getQASuccess(payload));
                successCB && successCB(res);
            })
            .catch((err) => {
                dispatch(getQAError(err));
                dispatch(setMessage(err?.data?.message));
            });
    } catch (error) {
        console.log('error');
        dispatch(getQAError('Something went wrong'));
    }
};

const getAllFAQ =
    (rowsPerPage = 10, searchData = '', page = 1) =>
        (dispatch) => {
            dispatch(getAllFAQStart());
            try {
                http(
                    process.env.REACT_APP_ROOT_URL_FAQ,
                    'GET',
                    `documents-service/faqs-service/faq-categories?pageNumber=${page}&pageSize=${rowsPerPage}&searchKey=${searchData}`,
                    true
                )
                    .then((res) => {
                        const payload = {
                            allFAQs: res
                        };
                        dispatch(getAllFAQSuccess(payload));
                    })
                    .catch((err) => {
                        dispatch(getAllFAQError(err));
                        dispatch(setMessage(err?.data?.message));
                    });
            } catch (error) {
                dispatch(getAllFAQError('Something went wrong'));
            }
        };

const getAllQA =
    (id, rowsPerPage = 10, searchData = '', page = 1) =>
        (dispatch) => {
            dispatch(getAllQAStart());
            try {
                http(
                    process.env.REACT_APP_ROOT_URL_FAQ,
                    'GET',
                    `documents-service/faqs-service/faqs/category/${id}?searchKey=${searchData}&pageNumber=${page}&pageSize=${rowsPerPage}`,
                    true
                )
                    .then((res) => {
                        const payload = {
                            allQAs: res
                        };
                        dispatch(getAllQASuccess(payload));
                    })
                    .catch((err) => {
                        dispatch(getAllQAError(err));
                        dispatch(setMessage(err?.data?.message));
                    });
            } catch (error) {
                dispatch(getAllQAError('Something went wrong'));
            }
        };

