import React from 'react';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { FormProvider, RHFTextField } from '../../../../components/hook-form';
import * as yup from 'yup';
import CloseIcon from '@material-ui/icons/Close';

import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, Grid, Link, Divider } from '@material-ui/core';
import { useDispatch } from 'react-redux';
import { addQA, updateQA, getAllQA } from '../../../../store/actions/FAQActions';
import useStyles from '../../../DialogStyles';

const validationSchema = yup.object({
    question: yup.string('Enter Question here').required('Question is required'),
    answer: yup.string('Enter Answer').required('Answer is required')
});

export default function AddUpdateQA(props) {
    const { openAddUpdateQAs, setOpenAddUpdateQAs, isEditData, QAId } = props;

    const classes = useStyles();
    const dispatch = useDispatch();

    const initialValues = {
        question: isEditData?.question || '',
        answer: isEditData?.answer || ''
    };
    const methods = useForm({
        mode: 'onChange',
        resolver: yupResolver(validationSchema),
        defaultValues: initialValues
    });
    const { reset, handleSubmit } = methods;

    const handlePopupClose = () => {
        setOpenAddUpdateQAs(false);
        reset();
    };
    const onSubmit = (data) => {
        const payload = {
            question: data?.question,
            answer: data?.answer
        };

        if (isEditData) {
            dispatch(
                updateQA(payload, isEditData.id, () => {
                    dispatch(getAllQA(QAId));
                })
            );
            handlePopupClose();
        } else {
            dispatch(
                addQA(payload, QAId, () => {
                    dispatch(getAllQA(QAId));
                })
            );
            handlePopupClose();
        }
    };

    return (
        <div>
            <Dialog open={openAddUpdateQAs} classes={{ paper: classes.dialog }}>
                <DialogTitle className={classes.dialogTitle}>
                    <div className={classes.dialogTitleChild}>
                        <Typography style={{ fontSize: '20px' }}>{!isEditData ? 'Add QA' : 'Update QA'}</Typography>
                        <Link to="#" onClick={handlePopupClose}>
                            <CloseIcon className={classes.closeIcon} />
                        </Link>
                    </div>
                </DialogTitle>
                <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
                    <DialogContent>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Question <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="question" placeholder="Question here" className={classes.textField} autoFocus />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <label className={classes.label}>
                                    Answer <span className={classes.mandatory}> * </span>
                                </label>
                                <RHFTextField name="answer" placeholder="Answer Here" className={classes.textField} />
                            </Grid>
                        </Grid>
                    </DialogContent>
                    <Divider style={{ marginTop: 15 }} />
                    <DialogActions className={classes.dialogAction}>
                        <Button variant="outlined" color="primary" className={classes.buttonCancel} onClick={handlePopupClose}>
                            Close
                        </Button>
                        <Button variant="contained" color="primary" type="submit" className={classes.buttonSubmit}>
                            {!isEditData ? 'Add' : 'Update'}
                        </Button>
                    </DialogActions>
                </FormProvider>
            </Dialog>
        </div>
    );
}
